	export default {
		data() {
			return {
				tabBars: [
					{name:'未使用',id:0},
					{name:'已使用',id:1},
					{name:'已失效',id:2},
				],
				scrollLeft: 0,
				tabIndex: 0,
				symbolId: 0,//分类ids
				lists:[],
				loadingType: 0,
				next : -1,
				start: 0,
				size: 15,
				record: false,
				bottom: false,
				not:true,
				ready:false,
				authKey:"",
                token:"",
				code:''
			};
		},
		onLoad: function() {
			this.authKey = uni.getStorageSync('authKey');
		},
		onShow() {
			this.start = 0
			this.requestcouponsList();
		},
		onShareAppMessage(res) {
			console.log(res)
		    if (res.from === 'button') {
		       // 来自页面内转发按钮
		       console.log(res.target)
		    }
		    return {
		       title: '您的好友送您一张优惠券，快来领取',
		       // path: "https://zct.51kdd.com/pages/knowledge/knowledge?token=" + this.token,
		       path: "/pages/knowledge/knowledge?token=" + this.token,
		       imageUrl: 'http://test.51kdd.com/share.png',
			}
		},
		methods:{
			getElSize(id) { //得到元素的size
				console.log(id)
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data)
					}).exec()
				})
			},
			async tapTab(e) { //点击tab-bar
				this.symbolId = e.currentTarget.id
				if(this.symbolId == 0){
					this.not = true
					this.ready = false
				}else{
					this.ready = true
					this.not = false
				}
				if (this.tabIndex === e.target.dataset.current) {
					return false;
				} else {
					let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = e.target.dataset.current;
				}
				this.start = 0
				this.requestcouponsList()
			},
			// 赠送
			gived(e){
				console.log('真实故事')
				uni.request({
					url: this.http+'home/giveCoupon/' + e,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
					},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							this.token = res.data.data.token
							
						}
					},
				});
			},
			goSever(){
				uni.reLaunch({
					url: '../../service/service'
				});
			},
			 // 请求订单的列表
				requestcouponsList: function(isLoading = true) {
					var that = this;
					let querys = "?start=" + this.start + "&size=" + this.size 
					uni.request({
						url: this.http+'home/coupons' + querys,
						method: 'GET',
						header: {
							authKey: this.authKey
						},
						data: {
							type:that.symbolId
						},
						success: res => {
							let data = res.data.data
							if (res.data.code == 200) {
								let order = data.list
								if (order.length == 0) {
									that.loadingType = 2
									order = []
								}
								else if (order.length < that.size)
									that.loadingType = 2
								else 
									that.loadingType = 0
								
								if (that.start == 0) 
									that.lists = []
			                    that.start = data.next	
								that.lists = that.lists.concat(order)
								if (that.lists.length == 0) 
									that.record = true
								else 
									that.record = false
								
							}
							
						},
					});
				},
				lower(){
					if (this.loadingType == 2) {
						return
					}
					this.requestcouponsList(false)
				}
		}
	}
